.container {
  background: #f0f2f5;
  background-image: url("@/assets/login_background.svg");
  width: 100%;
  min-height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  padding: 110px 0 144px 0;
  position: relative; }

.top {
  text-align: center; }

.header {
  height: 44px;
  line-height: 44px; }
  .header a {
    text-decoration: none; }

.logo {
  height: 44px;
  vertical-align: top;
  margin-right: 16px; }

.title {
  font-size: 33px;
  color: fade(#000, 85%);
  font-family: 'Myriad Pro', 'Helvetica Neue', Arial, Helvetica, sans-serif;
  font-weight: 600;
  position: relative;
  top: 2px; }

.desc {
  font-size: 14px;
  color: fade(#000, 45%);
  margin-top: 12px;
  margin-bottom: 40px; }

.footer {
  position: absolute;
  width: 100%;
  bottom: 0; }



