.main {
  width: 368px;
  margin: 0 auto; }
  .main :global .ant-form-item {
    margin-bottom: 24px; }
  .main h3 {
    font-size: 16px;
    margin-bottom: 20px; }
  .main .mobileGroup :global .ant-form-item {
    margin-bottom: 0;
    vertical-align: top; }
    .main .mobileGroup :global .ant-form-item:first-child .ant-select-selection {
      border-right-width: 0;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0; }
    .main .mobileGroup :global .ant-form-item:last-child .ant-input {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0; }
  .main .getCaptcha {
    display: block;
    width: 100%; }
  .main .submit {
    width: 50%; }
  .main .login {
    float: right;
    line-height: 40px; }

.success, .warning, .error {
  -webkit-transition: color .3s;
  -o-transition: color .3s;
  transition: color .3s; }

.success {
  color: #61D43E; }

.warning {
  color: gold; }

.error {
  color: #FF402F; }

.progress-pass > .progress :global .ant-progress-bg {
  background-color: gold; }



