.result {
  text-align: center;
  width: 72%;
  margin: 0 auto; }
  .result .icon {
    font-size: 72px;
    line-height: 72px;
    margin-bottom: 24px; }
    .result .icon > .success {
      color: #61D43E; }
    .result .icon > .error {
      color: #FF402F; }
  .result .title {
    font-size: 24px;
    color: fade(#000, 85%);
    font-weight: 500;
    line-height: 32px;
    margin-bottom: 16px; }
  .result .description {
    font-size: 14px;
    line-height: 22px;
    color: fade(#000, 45%);
    margin-bottom: 24px; }
  .result .extra {
    background: #fafafa;
    padding: 24px 40px;
    border-radius: 2px;
    text-align: left; }
  .result .actions {
    margin-top: 32px; }
    .result .actions button:not(:last-child) {
      margin-right: 8px; }



