.list {
  max-height: 400px;
  overflow: auto; }
  .list .item {
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    overflow: hidden;
    cursor: pointer;
    padding-left: 24px;
    padding-right: 24px; }
    .list .item .meta {
      width: 100%; }
    .list .item .avatar {
      background: #fff;
      margin-top: 4px; }
    .list .item.read {
      opacity: .4; }
    .list .item:last-child {
      border-bottom: 0; }
    .list .item:hover {
      background: #e6f7ff; }
    .list .item .title {
      font-weight: normal;
      margin-bottom: 8px; }
    .list .item .description {
      font-size: 12px;
      line-height: 1.5; }
    .list .item .datetime {
      font-size: 12px;
      margin-top: 4px;
      line-height: 1.5; }
    .list .item .extra {
      float: right;
      color: fade(#000, 45%);
      font-weight: normal;
      margin-right: 0;
      margin-top: -1.5px; }

.notFound {
  text-align: center;
  padding: 73px 0 88px 0;
  color: fade(#000, 45%); }
  .notFound img {
    display: inline-block;
    margin-bottom: 16px;
    height: 76px; }

.clear {
  height: 46px;
  line-height: 46px;
  text-align: center;
  color: fade(#000, 65%);
  border-radius: 0 0 4px 4px;
  border-top: 1px solid hsv(0, 0, 91%);
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  cursor: pointer; }
  .clear:hover {
    color: fade(#000, 85%); }



